import React from "react";
import background from "./assets/temp_background.jpg";
import linkedin from "./assets/linkedin.svg";
import gmail from "./assets/gmail.svg";
import github from "./assets/github.svg";
import instagram from "./assets/instagram.svg";

const Home = () => {
  return (
    <div className="relative flex flex-col items-center">
      <img src={background} alt="Background" className="w-full" />
      <div className="absolute top-2/3 w-1/3 rounded-md flex flex flex-row justify-evenly items-center py-2 bg-yellow-100 bg-opacity-25">
        <a
          href="https://www.linkedin.com/in/dz365"
          target="_blank"
          rel="noreferrer"
        >
          <img src={linkedin} className="w-16" alt="LinkedIn" />
        </a>
        <a href="https://www.github.com/dz365" target="_blank" rel="noreferrer">
          <img src={github} className="w-16" alt="GitHub" />
        </a>
        <a href="mailto:dwqz365@gmail.com" target="_blank" rel="noreferrer">
          <img src={gmail} className="w-16" alt="Email" />
        </a>
        <a
          href="https://www.instagram.com/dz.2372"
          target="_blank"
          rel="noreferrer"
        >
          <img src={instagram} className="w-16" alt="Instagram" />
        </a>
      </div>
    </div>
  );
};

export default Home;
